<template>
  <div class="pa-4">
    <div class="d-flex align-start">
      <h1 class="mr-2">
        日期组件国际化
      </h1>
    </div>
    <v-row>
      <v-col class="d-flex align-center">
        <v-select
          v-model="lang"
          :items="['zhHans', 'en', 'ja']"
          style="max-width: fit-content;"
          class="mt-2 mr-2"
          dense
          small
          outlined
          hide-details
          clearable
          @change="changeLang(lang)"
        />
        <span
          class="mr-2"
        >
          {{ $t('msg') }}
        </span>
      </v-col>
    </v-row>
    <v-form ref="form">
      <v-row>
        <v-col class="d-flex align-center">
          <v-text-field
            v-model="date"
            type="datetime-local"
            step="1"
            style="max-width: fit-content;"
            class="mr-2"
            dense
            small
            outlined
            hide-details
          />
          <input
            v-model="date"
            timezone="-12:00"
            type="datetime-local"
            style="border: 1px solid;border-radius: 5px;"
            class="mr-2"
          >
          <span class="mr-2">date:{{ date }}</span>
        </v-col>
      </v-row>
    </v-form>
    <div>
      <p>
        <a href="https://stackoverflow.com/questions/7372038/is-there-any-way-to-change-input-type-date-format">有没有办法改变输入类型=“日期”格式？ stackoverflow</a>
      </p>
      <p>
        结论：根据操作系统的语言地区设置，原生input[type="datetime-local"]显示不同的日期选项 
      </p>
      <p>
        和i18n无法直接配合使用
      </p>
      <p>
        建议用第三方库实现，日期选择的国际化
      </p>
    </div>
    <div>
      <img
        src="./en.png"
        alt="en"
      >
      <img
        src="./ensys.png"
        alt="ensys"
      >
      <hr>
      <img
        src="./cn.png"
        alt="cn"
      >
      <img
        src="./cnsys.png"
        alt="cnsys"
      >
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      lang: 'zhHans',
      date: '',
    };
  },
  methods: {
    changeLang(lang) {
      this.lang = lang
      this.$i18n.locale = lang
    },
  },
}
</script>

